
.csDialog[data-v-2ef5cbfa]{
.csDialog-bg{
    position: fixed;
    top: 0;  left: 0;
    height: 100%; width: 100%;
    z-index: 50;
}
.csDialog-alert{
    position: fixed;
    top: 50%;  left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    margin: auto;
    width: 5.92rem;
    z-index: 99;
    background-color: transparent;
.csDialog-alert-box{
    background-color: #fff;
    overflow: hidden;
.csDialog-content{
    padding: 0.66rem 0.48rem;
    border-bottom: 0.01rem solid #f4f4f4;
    text-align: center;
span{
    display: inline-block;
    text-align: left;
    font-size: 0.32rem;
}
}
.csDialog-btn{
    padding: 0.26rem 0;
    text-align: center;
    font-size: 0.36rem;
    color: var(--color);
}
.csDialog-btn:active{
    background-color: #eee;
}
}
}
.csDialog-confirm{
    position: fixed;
    top: 50%;  left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    margin: auto;
    width: 5.92rem;
    z-index: 99;
.csDialog-confirm-box{
    background-color: #fff;
    overflow: hidden;
    border: 0.01rem solid #f4f4f4;
.csDialog-content{
    padding: 0.66rem 0.48rem;
    border-bottom: 0.01rem solid #f4f4f4;
    text-align: center;
span{
    display: inline-block;
    text-align: left;
    font-size: 0.32rem;
}
}
.csDialog-btn{
    float: left;
    width: 49.8%;
    padding: 0.26rem 0;
    text-align: center;
    font-size: 0.36rem;
}
div:nth-of-type(1){
    border-right: 0.01rem solid #f4f4f4;
}
.csDialog-btn-right {
    color: var(--color);
}
.csDialog-btn-left:active{
    background-color: #eee;
}
.csDialog-btn-right:active{
    background-color: #eee;
}
}
}
//淡入淡出
  .animation-fade-enter-active {
      -webkit-animation-name: fade-in;
              animation-name: fade-in;
      -webkit-animation-duration: 0.28s;
              animation-duration: 0.28s;
}
.animation-fade-leave-active {
    -webkit-animation-name: fade-out;
            animation-name: fade-out;
    -webkit-animation-duration: 0.28s;
            animation-duration: 0.28s;
}
@-webkit-keyframes fade-in {
0% {
        -webkit-transform: scale3d(0, 0, 0);
        transform: scale3d(0.5, 0.5, 0.5);
        opacity: 0;
}
50% {
        -webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
        animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
}
100% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        -webkit-animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
        animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
        opacity: 1;
}
}
@keyframes fade-in {
0% {
        -webkit-transform: scale3d(0, 0, 0);
        transform: scale3d(0.5, 0.5, 0.5);
        opacity: 0;
}
50% {
        -webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
        animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
}
100% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        -webkit-animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
        animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
        opacity: 1;
}
}
@-webkit-keyframes fade-out {
0% {
        opacity: 1;
}
100% {
        opacity: 0;
        -webkit-transform: scale3d(0.9, 0.9, 1);
        transform: scale3d(0.9, 0.9, 1);
}
}
@keyframes fade-out {
0% {
        opacity: 1;
}
100% {
        opacity: 0;
        -webkit-transform: scale3d(0.9, 0.9, 1);
        transform: scale3d(0.9, 0.9, 1);
}
}
}
